import React from "react";

import "./Toggle.scss";

export default function Toggle({ onLabel, offLabel, onClick }) {
	return (
		<label class="toggle">
			<input type="checkbox" onClick={onClick} />
			<span class="slider"></span>
			<span class="labels" data-on={onLabel} data-off={offLabel}></span>
		</label>
	);
}
