import React, { useState, useRef } from "react";

import "./VideoPlayer.scss";

export function VideoPlayer({ src }) {
	const [isPlaying, setIsPlaying] = useState(false);
	const [currentTime, setCurrentTime] = useState(0);
	const [duration, setDuration] = useState(0);
	const videoRef = useRef(null);

	const handlePlayPause = () => {
		if (isPlaying) {
			videoRef.current.pause();
		} else {
			videoRef.current.play();
		}
		setIsPlaying(!isPlaying);
	};

	const handleTimeUpdate = () => {
		setCurrentTime(videoRef.current.currentTime);
	};

	const handleLoadedMetadata = () => {
		setDuration(videoRef.current.duration);
	};

	const handleProgressClick = (e) => {
		const progressWidth = e.target.clientWidth;
		const clickX = e.nativeEvent.offsetX;
		const duration = videoRef.current.duration;
		videoRef.current.currentTime = (clickX / progressWidth) * duration;
	};

	return (
		<div className="video-player">
			<video
				ref={videoRef}
				src={require(`../../assets/${src.replace("@", "")}`)}
				onTimeUpdate={handleTimeUpdate}
				onLoadedMetadata={handleLoadedMetadata}
			></video>
			<div className="controls">
				<button onClick={handlePlayPause}>{isPlaying ? "Pause" : "Play"}</button>
				<div className="progress" onClick={handleProgressClick}>
					<div
						className="progress-bar"
						style={{
							width: `${(currentTime / duration) * 100}%`,
						}}
					></div>
				</div>
				<div className="time">
					{formatTime(currentTime)} / {formatTime(duration)}
				</div>
			</div>
		</div>
	);
}

const formatTime = (timeInSeconds) => {
	const minutes = Math.floor(timeInSeconds / 60);
	const seconds = Math.floor(timeInSeconds % 60);
	return `${String(minutes).padStart(2, "0")}:${String(seconds).padStart(2, "0")}`;
};
