import React from "react";

import ReactPlayer from "react-player";
import ImageGallery from "react-image-gallery";
import { VideoPlayer } from "../VideoPlayer/VideoPlayer";

import "./BlogSection.scss";

export default function BlogSection({ type, title, style, text, image, video, list }) {
	function renderImage(items) {
		return (
			<ImageGallery
				showFullscreenButton
				additionalClass="blog-section-image"
				useBrowserFullscreen={false}
				showPlayButton={false}
				showNav={items.length < 2}
				showThumbnails={items.length >= 2}
				onClick={(e) => {
					// document.querySelector(`#${src} .image-gallery-icon`).click();
				}}
				items={items}
			/>
		);
	}

	return (
		<div className="blog-section" style={style || {}}>
			{title && <h3 className="blog-section-title">{title}</h3>}
			{typeof text === "string" ? (
				<p className="blog-section-text">{text}</p>
			) : (
				typeof text === "object" && (
					<p className="blog-section-text" style={text.style}>
						{text.content}
					</p>
				)
			)}
			{type === "list" && (
				<ul className="blog-section-list">
					{list.map((item, i) => (
						<li key={i} className="blog-section-list-item">
							{item}
						</li>
					))}
				</ul>
			)}
			{typeof image === "string"
				? renderImage([
						{
							original: require(`../../assets/${image.replace("@", "")}`),
							thumbnail: require(`../../assets/${image.replace("@", "")}`),
						},
				  ])
				: typeof image === "object" &&
				  renderImage(
						[
							{
								original: require(`../../assets/${image.src.replace("@", "")}`),
								thumbnail: require(`../../assets/${image.src.replace("@", "")}`),
								thumbnailClass: "",
							},
							image.hover && {
								original: require(`../../assets/${image.hover.replace("@", "")}`),
								thumbnail: require(`../../assets/${image.hover.replace("@", "")}`),
							},
						].filter(Boolean)
				  )}
			{typeof video === "string" ? (
				<ReactPlayer
					className="blog-video-player"
					volume={0}
					muted={true}
					controls
					url={require(`../../assets/${video.replace("@", "")}`)}
				/>
			) : (
				typeof video === "object" && <VideoPlayer src={video.src} />
			)}
		</div>
	);
}
